<template>

<swiper class="swiper" :options="swiperOption" ref='mySwiper' @slideChange="changeSwiperIndex">

    <swiper-slide class="slide-1">
        <h1 class="main-title">Ink Lingerie</h1>
    </swiper-slide>
    <swiper-slide class="slide-2"> 
        <h1 class="main-title">Ink Lingerie</h1>
    </swiper-slide>
    <swiper-slide class="slide-3"> 
        <h1 class="main-title">Ink Lingerie</h1>
    </swiper-slide>
    
    <div class="swiper-pagination swiper-pagination-white" slot="pagination"></div>
    <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
    <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
</swiper>

    
</template>


<script>
  import { Swiper, SwiperSlide } from 'vue-awesome-swiper'

  import '../../node_modules/swiper/swiper.scss'

  export default {
    name: 'swiper-example-carousel-mode',
    title: 'Auto slides per view / Carousel mode',
    components: {
      Swiper,
      SwiperSlide,
    },
    props: {
        swiperId: Number,
        watchIndex: Function,
    },
    data() {
      return {
        swiperOption: {
            slidesPerView: 'auto',
          centeredSlides: true,
          spaceBetween: 60,
          pagination: {
            el: '.swiper-pagination',
            clickable: true
          },
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev'
          }
        },
      }
    },
    methods: {
        changeSwiperIndex() { 
           let index =  this.$refs.mySwiper.$swiper.activeIndex;
           if(this.$refs.mySwiper.$swiper.touchEventsData.isTouched) {
               alert('isScrolling')
           }
           
           this.watchIndex(index);
        }
    }    
  }
</script>


