import Vue from 'vue'
import App from './App.vue'
import Parallax from 'parallax-js'


Vue.config.productionTip = false


new Vue({
  render: h => h(App),
}).$mount('#app')



// DRAG ME TEXT ABOVE THE MOUSE CURSOR
function mouse() {
  'use strict';

  let boxElem = document.querySelector('.swiper-container');
  let pointerElem = document.createElement('div');
  pointerElem.classList.add('dragme-text');
  pointerElem.innerHTML ='Drag Me';
  boxElem.appendChild(pointerElem);

  let coord = boxElem.getBoundingClientRect();
  
  boxElem.addEventListener('mousemove', (e) => {
    pointerElem.style.opacity = "1"
    pointerElem.style.top = `${e.pageY - coord.y - 10}px`;
    pointerElem.style.left = `${e.pageX - (window.innerWidth / 2) + 30}px`;
  })

  boxElem.addEventListener('mouseleave', (e) => {
    pointerElem.style.opacity = "0"

  });
};

mouse();

// TURN ON THE PARALLAX ON THE DESCTOP
  
  if (window.innerWidth > 768) {
    
    let scene = document.getElementById('scene');
    let parallaxInstance = new Parallax(scene);
    
  }
  
// MENU MOBILE

document.querySelector('.header__nav-mob').addEventListener('click', (e) => {
  let menu = document.querySelector('.header__nav > ul');

  if (!e.target.classList.contains('active')) {
    e.target.classList.add('active');
    menu.classList.add('mob');
    
  } else {
    e.target.classList.remove('active');
    menu.classList.remove('mob');
  }
})


// ANIMATION

window.onload = () => {
  document.querySelector('.back-svg > path').style.animation = 'dash 2s linear forwards';
  setTimeout(() => {
    document.querySelector('header').classList.add('to-show');
    document.querySelector('footer').classList.add('to-show');
  }, 1000)
  setTimeout(() => {
    document.querySelector('.img-block').classList.add('to-show');
  }, 2000)
  setTimeout(() => {
    document.querySelector('.swiper').classList.add('to-show');
  }, 3000)
  
  
}