    },
    data() {
        return {
            currentIndex: 0,
            imgUrl01: img01,
            imgUrl02: img02,
            imgUrl03: img03,
        }
    },
    watch: {
        'swiperId' : function(value) {
            this.currentIndex = value;
            document.querySelectorAll(".img-wrapper").forEach(e => {
                if (Number(e.id) == value) {
                    e.style.opacity = '1';
                } else {
                    e.style.opacity = '0';
                }
                
            });
            
        }
              
    }

}
